import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./RecordingForm.css";

export default function RecForm() {
  const [currentStep, setCurrentStep] = useState(1);

  const handleNextClick = () => {
    setCurrentStep(currentStep + 1);
  };

  const handlePreviousClick = () => {
    setCurrentStep(currentStep - 1);
  };
  const [dateTime, setDateTime] = useState("");

  const handleDateTimeChange = (event) => {
    setDateTime(event.target.value);
  };

  return (
    <section className="RecordingFormSec">
      <form id="msform">
        <ul id="progressbar">
          <li className={currentStep === 1 ? "active" : ""}>
              
          </li>
          <li className={currentStep === 2 ? "active" : ""}> </li>
          <li className={currentStep === 3 ? "active" : ""}> </li>
        </ul>

        <fieldset style={{ display: currentStep === 1 ? "block" : "none" }}>
          <h2 className="fs-title">  </h2>
          <h3 className="fs-subtitle"> 1</h3>
          <input type="text" name="email" placeholder=" Email" />
          <input type="password" name="pass" placeholder="" />
          <input
            type="password"
            name="cpass"
            placeholder=" "
          />
          {currentStep === 1 && (
            <button
              type="button"
              className="next action-button"
              onClick={handleNextClick}
            >
              
            </button>
          )}
        </fieldset>

        <fieldset style={{ display: currentStep === 2 ? "block" : "none" }}>
          <h2 className="fs-title">  </h2>
          <h3 className="fs-subtitle">  </h3>
          <input
            type="datetime-local"
            id="appointmentDateTime"
            name="appointmentDateTime"
            value={dateTime}
            onChange={handleDateTimeChange}
            required
          />
          <input type="text" name="name" placeholder=" " />
          <input type="text" name="cername" placeholder=" " />
          <input type="text" name="cername" placeholder=" " />
          {currentStep === 2 && (
            <>
              <button
                type="button"
                className="previous action-button"
                onClick={handlePreviousClick}
              >
                
              </button>
              <button
                type="button"
                className="next action-button"
                onClick={handleNextClick}
              >
                
              </button>
            </>
          )}
        </fieldset>

        <fieldset style={{ display: currentStep === 3 ? "block" : "none" }}>
          <h2 className="fs-title"></h2>
          <h3 className="fs-subtitle">   </h3>
          <input type="text" name="phone" placeholder="" />
          <textarea name="address" placeholder=" "></textarea>
          {currentStep === 3 && (
            <>
              <button
                type="button"
                className="previous action-button"
                onClick={handlePreviousClick}
              >
                
              </button>
              <Link to="/">
                <button type="button" className="next action-button">
                  
                </button>
              </Link>
            </>
          )}
        </fieldset>
      </form>
    </section>
  );
}
